import React,{Component} from 'react';
import {
  TabBarIOS,
  TabBarItem,
  View,
  Text,
  AppRegistry
} from 'react-native';

class TabbarDemo extends React.Component{
  render(){
      return(
        <TabBarIOS>
          unselectedTintColor = 'yellow'
          tintColor = 'white'
          barTintColor = 'darkslateblue'
          <TabBarItem>
            title = 'blue'
            systemIcon = 'history'
            {this._renderContent}
          </TabBarItem>
        </TabBarIOS>
      );
  }
}

//返回视图
_renderContent = ()=>{
  return(
    <View>
      <text>测试数据</text>
    </View>
  );
};

AppRegistry.registerComponent('TabbarDemo',()=> TabbarDemo);
